<style scoped lang="less">
  .page {
    width: 100%;
    height: 100%;
    background: url('../../assets/bg.jpg');
    background-size: 100%;
    cursor: pointer;
  }

  .login {
    background: white;
    width: 350px;
    top: 200px;
    span {
      font-size: 18px;
    }
  }

</style>

<template>
  <div class="page">
    <Row loading>
      <Col :xs="0" :sm="12" :md="12" :lg="16">
        &nbsp;
      </Col>

      <Col :xs="24" :sm="10" :md="10" :lg="6">
        <Card class="login">
          <h2> 公众平台管理后台</h2>
          <Tabs :value="loginType">
            <TabPane label="账号登录" name="account">
              <Form :model="accountData" :rules="ruleInline">
                <FormItem prop="user">
                  <Input type="text" v-model="accountData.mobile" placeholder="手机号">
                    <Icon type="ios-person-outline" slot="prepend"></Icon>
                  </Input>
                </FormItem>
                <FormItem prop="password">
                  <Input type="password" v-model="accountData.password" placeholder="密码">
                    <Icon type="ios-lock-outline" slot="prepend"></Icon>
                  </Input>
                </FormItem>
                <FormItem>
                  <Button type="primary" @click="accountLoginBtn" long>登录</Button>
                </FormItem>
              </Form>
            </TabPane>
            <TabPane label="验证码登录" name="code">
              <Form :model="smsData" :rules="ruleCode">
                <FormItem prop="user">
                  <Input type="text" v-model="smsData.mobile" placeholder="手机号">
                    <Icon type="ios-person-outline" slot="prepend"></Icon>
                  </Input>
                </FormItem>
                <FormItem inline="true" prop="password">

                  <Row>
                    <Col span="14">
                      <Input v-model="smsData.code" placeholder="验证码">
                        <Icon type="ios-lock-outline" slot="prepend"></Icon>
                      </Input>
                    </Col>
                    <Col span="8" offset="1">
                      <sms-code ref="code" :phone="smsData.mobile"></sms-code>
                    </Col>
                  </Row>

                </FormItem>
                <FormItem>
                  <Button type="primary" @click="codeLoginBtn()" long>登录</Button>
                </FormItem>
              </Form>
            </TabPane>
          </Tabs>
          <Spin size="large" fix v-if="load"></Spin>
        </Card>

      </Col>
    </Row>

  </div>
</template>

<script>
import smsCode from '../../components/smsCode'
import api from './api'

export default {
  name: 'login',
  components: {
    smsCode
  },
  data () {
    return {
      load: false,
      loginType: 'account',
      accountData: {
        mobile: '',
        password: ''
      },
      smsData: {
        mobile: '',
        code: ''
      },
      ruleInline: {
        mobile: [
          { required: true, message: '请输入手机号', trigger: 'blur' }
        ],
        password: [
          { required: true, message: '请输入密码', trigger: 'blur' },
          {
            type: 'string',
            min: 6,
            message: '密码不能少于6位',
            trigger: 'blur'
          }
        ]
      },
      ruleCode: {
        mobile: [
          { required: true, message: '请输入手机号', trigger: 'blur' }
        ],
        code:
            [
              { required: true, message: '请输入验证码', trigger: 'blur' }
            ]
      }
    }
  },
  mounted () {
    this.$store.dispatch('logout')
  },
  methods: {
    login (res) {
      this.$store.dispatch('login', res.data)
      this.$router.push({
        name: 'home'
      })
    },
    accountLoginBtn () {
      if (!this.$formUtil.isPoneAvailable(this.accountData.mobile)) {
        this.$Message.error('请输入正确的手机号')
        return
      }
      if (!this.$formUtil.isStrValid(this.accountData.password)) {
        this.$Message.error('请输入密码')
        return
      }
      this.load = true
      api.accountLogin(this.accountData, res => {
        this.load = false
        this.login(res)
      }, res => {
        this.load = false
        if (parseInt(res.data.code) === 302) {
          this.toCode()
        }
      })
    },
    codeLoginBtn () {
      if (!this.$formUtil.isPoneAvailable(this.smsData.mobile)) {
        this.$Message.error('请输入正确的手机号')
        return
      }
      if (!this.$formUtil.isStrValid(this.smsData.code)) {
        this.$Message.error('请输入验证码')
        return
      }
      this.load = true
      api.smsLogin(this.smsData, res => {
        this.load = false
        this.login(res)
      }, () => {
        this.load = false
      })
    },
    toCode () {
      this.smsData.mobile = this.accountData.mobile
      this.loginType = 'code'
      this.$refs.code.getCode(this.smsData.mobile)
    }
  }
}
</script>
