import Api from '@/js/api/baseApi'

export default class {
  static smsLogin (data, callback, errorBack) {
    Api.request(this.uri() + 'mobile_login', callback, errorBack, data)
  }

  static accountLogin (data, callback, errorBack) {
    Api.request(this.uri() + 'account_login', callback, errorBack, data)
  }

  static uri () {
    return Api.url() + 'admin/'
  }
}
