<template>
  <Button :loading="load" type="primary" :disabled="disable" @click="getCode(phone)">
    {{this.timer !==null?'请在'+count+'秒后重试':'获取短信验证码'}}
  </Button>
</template>

<script>
import Api from '../js/api/verify'

export default {
  name: 'smsCode',
  props: ['phone'],
  data () {
    return {
      timer: null,
      count: 60,
      disable: false,
      load: false
    }
  },
  methods: {
    code () {
    },
    getCode (phone) {
      let _this = this
      if (!this.timer) {
        if (this.isPoneAvailable(phone)) {
          this.load = true
          Api.smsCode(phone, () => {
            this.load = false
            console.log('123')
            _this.timerStart()
            this.$Message.success('验证码发送成功')
          }, () => {
            this.load = false
          })
        } else {
          this.message('请输入正确的手机号')
        }
      }
    },
    timerStart () {
      this.disable = true
      console.log('345')
      this.timer = setInterval(() => {
        if (this.count > 0 && this.count <= 60) {
          this.count--
        } else {
          clearInterval(this.timer)
          this.timer = null
          this.disable = false
          this.count = '60'
        }
      }, 1000)
    },

    isPoneAvailable (str) {
      const myreg = /^[1][3,4,5,6,7,8,9][0-9]{9}$/
      return myreg.test(str)
    },
    message (text) {
      this.$Message.error(text)
    }
  }

}
</script>

